import { LoginSignupAction, LogoutAction } from "../actions/ActionCreator";
import { createReducer } from "@reduxjs/toolkit";

export const AppReducer = createReducer(
  {
    apiUrl: "https://rest-api-v2.herokuapp.com",
    currentUser: {
      username: "",
      password: "",
      token: "",
      tasks: [],
    },
  },
  (builder) => {
    builder
      .addCase(LoginSignupAction, (state, action) => {
        state.currentUser = action.payload.userInput;
      })
      .addCase(LogoutAction, (state) => {
        state.currentUser = {
          username: "",
          password: "",
          token: "",
          tasks: [],
        };
      });
  }
);
