import React from "react";
import { Modal } from "antd";
import { useDispatch, useSelector } from "react-redux";
import {
  ModalVisibleAction,
  ModalLoadingAction,
  DeleteTaskAction,
} from "../redux/actions/ActionCreator";
import axios from "axios";

const ModalMain = () => {
  const dispatch = useDispatch();

  const confirmLoading = useSelector((state) => state.modalState.isLoading);
  const visible = useSelector((state) => state.modalState.isVisible);
  const modalContent = useSelector((state) => state.modalState.modalContent);
  const deleteId = useSelector((state) => state.taskState.deleteID);

  const handleCancel = () => {
    dispatch(ModalVisibleAction(false));
  };

  //thunk
  const handleOk = () => {
    dispatch(updateTaskListToStore());
  };
  function deleteTaskAPI(deleteId) {
    return axios.delete(
      `https://test-heroku444.herokuapp.com/tasks/${deleteId}`
    );
  }
  function updateTaskListToStore() {
    return function (dispatch) {
      return deleteTaskAPI(deleteId).then(() => {
        dispatch(DeleteTaskAction("confirmDelete", deleteId));
        dispatch(ModalLoadingAction(true));
        dispatch(ModalVisibleAction(false));
      });
    };
  }
  
  return (
    <Modal
      // title="Confirmation"
      visible={visible}
      confirmLoading={confirmLoading}
      onOk={handleOk}
      onCancel={handleCancel}
    >
      <p className="modalContent">{modalContent}</p>
    </Modal>
  );
};

export default ModalMain;
