import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { LoginAPI, SignUpAPI } from "../services/UserServices";
import {
  LoginSignupAction,
  ToggleLoadingAction,
} from "../redux/actions/ActionCreator";

const LoginPage = ({ toastSuccess, toastError }) => {
  const history = useHistory();
  const thunkDispatch = useDispatch();
  const apiUrl = useSelector((store) => store.appState.apiUrl);

  const [mode, setMode] = useState("Login");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [isClose, setIsClose] = useState(false);

  const handleUsernameChange = (e) => {
    setUsername(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const switchMode = () => {
    mode === "Login" ? setMode("Signup") : setMode("Login");
  };

  //login
  const LoginThunk = () => {
    thunkDispatch(ToggleLoadingAction(true));
    const userInput = { username, password };
    return function (dispatch) {
      return LoginAPI(apiUrl, userInput)
        .then((res) => {
          dispatch(LoginSignupAction(res.data));
          toastSuccess(`Welcome back, ${res.data.username}. Redirecting...`, {
            onClose: () => history.push("/"),
          });
          setIsClose(true);
        })
        .catch((error) => {
          toastError(error.response.data.message);
        })
        .finally(() => {
          thunkDispatch(ToggleLoadingAction(false));
        });
    };
  };
  const handleLogin = () => {
    isClose ? console.log("blocked") : thunkDispatch(LoginThunk());
  };

  //signup
  const SignupThunk = () => {
    thunkDispatch(ToggleLoadingAction(true));
    const userInput = { username, password };
    return function (dispatch) {
      return SignUpAPI(apiUrl, userInput)
        .then((res) => {
          console.log(res.data);
          dispatch(LoginSignupAction(res.data));
          toastSuccess(`Welcome, ${res.data.username}. Redirecting...`, {
            onClose: () => history.push("/"),
          });
          setIsClose(true);
        })
        .catch((error) => {
          toastError(error.response.data.message);
        })
        .finally(() => {
          thunkDispatch(ToggleLoadingAction(false));
        });
    };
  };
  const handleSignup = () => {
    isClose ? console.log("blocked") : thunkDispatch(SignupThunk());
  };

  //submit
  const handleSubmit = () => {
    mode === "Login" ? handleLogin() : handleSignup();
  };

  return (
    <>
      <header>
        <h1>Task Manager by React</h1>
        <h2>{mode === "Login" ? "Log in" : "Sign up"}</h2>
      </header>

      <main className="login-page">
        <div className="form-item">
          <label htmlFor="username">Username:</label>
          <input
            type="text"
            id="username"
            placeholder="demo: user"
            onChange={handleUsernameChange}
          ></input>
        </div>
        <div className="form-item">
          <label htmlFor="password">Password:</label>
          <input
            type="text"
            id="password"
            placeholder="demo: 123"
            onChange={handlePasswordChange}
          ></input>
        </div>
        <button
          onClick={handleSubmit}
          className="confirm-btn blue-text-btn btn"
        >
          {mode === "Login" ? "Log in" : "Sign up"}
        </button>

        <p className="sign-up-text">
          {mode === "Login" ? "Don't have an account yet?" : "Have an account?"}
          <span className="sign-up" onClick={switchMode}>
            {mode === "Login" ? " Sign up now!" : " Back to log in"}
          </span>
        </p>
      </main>
    </>
  );
};

export default LoginPage;
