import React from "react";
import { BiEditAlt } from "react-icons/bi";
import { RiDeleteBinLine } from "react-icons/ri";
import { MdDone } from "react-icons/md";
import { FaRedo } from "react-icons/fa";
import { useHistory } from "react-router-dom";
import {
  ModalContentAction,
  ModalVisibleAction,
  DeleteTaskAction,
  CompleteTaskAction,
  ToggleLoadingAction,
} from "../../redux/actions/ActionCreator";
import { useDispatch } from "react-redux";
import axios from "axios";

const Task = ({ task, toastSuccess, toastWarning }) => {
  const history = useHistory();
  const dispatch = useDispatch();

  //timezone
  var options = {
    timeZone: "Asia/Ho_Chi_Minh",
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
  };
  var formatter = new Intl.DateTimeFormat([], options);
  var timeFromServer = task.taskDate;
  var localTime = formatter.format(new Date(timeFromServer));

  //complete task thunk
  const completeTaskAPI = (id, isDone) => {
    return axios.patch(`https://test-heroku444.herokuapp.com/tasks/${id}`, {
      isDone: !isDone,
    });
  };
  const completeTaskToStore = () => {
    dispatch(ToggleLoadingAction(true));
    return function (dispatch) {
      return completeTaskAPI(task.id, task.isDone).then((res) => {
        dispatch(CompleteTaskAction(res.data));
        dispatch(ToggleLoadingAction(false));
        res.data.isDone
          ? toastSuccess("Task is completed")
          : toastWarning("Task is uncompleted");
      });
    };
  };
  const handleComplete = () => dispatch(completeTaskToStore());

  //Trigger modal delete
  const TriggerModalDelete = () => {
    dispatch(DeleteTaskAction("attemptDelete", task.id));
    dispatch(ModalContentAction("Do you want to delete this task"));
    dispatch(ModalVisibleAction(true));
  };

  return (
    <div className="taskItem">
      <div className="taskContent">
        <h3 className="taskName">{task.taskName}</h3>
        <h4 className="taskDate">{localTime}</h4>
      </div>
      <button className="doneBtn" onClick={handleComplete}>
        {task.isDone ? <FaRedo /> : <MdDone />}
      </button>

      <button
        className="editBtn"
        onClick={() => history.push(`/edit-task/${task.id}`)}
      >
        <BiEditAlt />
      </button>
      <button className="deleteBtn" onClick={TriggerModalDelete}>
        <RiDeleteBinLine />
      </button>
    </div>
  );
};

export default Task;
