import "./App.css";
import Home from "./views/Home/Home";
import axios from "axios";
import { useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import ModalMain from "./components/ModalMain";
import AddTask from "./views/AddTask";
import Loading from "./components/Loading";
import { useDispatch, useSelector } from "react-redux";
import EditTask from "./views/EditTask";
import {
  ToggleLoadingAction,
  GetTaskListAction,
} from "./redux/actions/ActionCreator";
import { ToastContainer, toast, Flip } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import LoginPage from "./views/LoginPage";
import TheNavigation from "./components/TheNavigation";
import Profile from "./views/Profile";

function App() {
  //toast
  const toastSuccess = (text, callback) => toast.success(text, callback);
  const toastError = (text) => toast.error(text);
  const toastWarning = (text) => toast.warn(text);

  //general
  const dispatch = useDispatch();

  //toggleLoading
  const isLoading = useSelector((store) => store.loadingState.isLoading);
  const toggleLoading = (mode) => {
    dispatch(ToggleLoadingAction(mode));
  };

  //thunk: get task list then update redux
  //to call this thunk: dispatch(updateTaskListToStore());
  const taskList = useSelector((store) => store.taskState.taskList);
  function getTaskList() {
    return axios.get("https://test-heroku444.herokuapp.com/tasks");
  }
  function updateTaskListToStore() {
    return function (dispatch) {
      return getTaskList().then((res) => dispatch(GetTaskListAction(res.data)));
    };
  }

  //first load: thunk call. After: re-render
  useEffect(() => {
    toggleLoading(true);
    dispatch(updateTaskListToStore());
    toggleLoading(false);
  }, []);

  return (
    <div className="App">
      <TheNavigation />
      <Loading isLoading={isLoading} />
      <ToastContainer
        transition={Flip}
        className="customizedToast"
        position="bottom-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop
        closeOnClick={false}
        rtl={false}
        pauseOnFocusLoss={false}
        draggable={false}
        pauseOnHover={false}
      />
      <ModalMain />
      <Switch>
        <Route path="/login">
          <LoginPage toastSuccess={toastSuccess} toastError={toastError} />
        </Route>
        <Route path="/profile">
          <Profile />
        </Route>
        <Route path="/add-task">
          <AddTask
            toggleLoading={toggleLoading}
            toastSuccess={toastSuccess}
            toastError={toastError}
          />
        </Route>
        <Route path="/edit-task/:id">
          <EditTask
            toggleLoading={toggleLoading}
            toastSuccess={toastSuccess}
            toastError={toastError}
          />
        </Route>
        <Route path="/">
          <Home
            tasks={taskList}
            toastSuccess={toastSuccess}
            toastWarning={toastWarning}
          />
        </Route>
      </Switch>
    </div>
  );
}

